<template>
  <div>
    <div class="bg">
      <div class="title">我要尽调</div>
      <Alert type="warning" show-icon>
        如没有尽调模板请先下载,
        <a
          style="color:#ff3534;"
          href="http://transee.net:12522/authorization/尽职调查评分表.doc"
        >点击下载尽调模板</a>
        <div class="cl"></div>
      </Alert>
      <br />
      <br />
      <div>
        <i-Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="150">
          <Form-Item label="选择尽调项目" prop="code">
            <i-Select v-model="formValidate.code" style="width:200px">
              <i-Option v-for="item in cityList" :value="item.cide" :key="item.id">{{ item.name}}</i-Option>
            </i-Select>
          </Form-Item>
          <form-item label="尽调模板" @submit.native.prevent prop="investPaper">
            <label>
              <Upload
                :action="fileconfig.fileUrl"
                :headers="fileconfig.fileHead"
                name="file"
                :on-success="investPaper"
                :show-upload-list="false"
              >
                <Button icon="ios-cloud-upload-outline">上传已填写的尽调模板</Button>
                <div v-if="formValidate.investPaper != ''">
                  <p>{{formValidate.investPaper}}</p>
                  <p style="color: #e25f5f;">*已添加,想更换直接上传新尽调模板</p>
                </div>
              </Upload>
            </label>
          </form-item>
          <Form-Item>
            <Button type="primary" @click="handleSubmit('formValidate')">提交尽调</Button>
          </Form-Item>
        </i-Form>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, Alert, Form, FormItem, Select, Option, Upload } from "iview";
import { surveyUpload } from "../../../../api/user";
import { messages, fileUploadUrl } from "../../../../utils";
export default {
  components: {
    [Button.name]: Button,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Select.name]: Select,
    [Option.name]: Option,
    [Upload.name]: Upload,
    [Alert.name]: Alert
  },
  data() {
    return {
      formValidate: {
        code: "",
        investPaper: ""
      },
      ruleValidate: {
        code: [
          {
            required: true,
            message: "项目,必须选择一个项目",
            trigger: "blur"
          }
        ],
        investPaper: [
          {
            required: true,
            message: "尽调文件,必须上传",
            trigger: "blur"
          }
        ]
      },
      cityList: [],
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl + "/survey",
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      }
    };
  },
  methods: {
    //上传lOGO
    investPaper(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.investPaper = res.data.url;
      }
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          surveyUpload(this.ruleValidate).then(res => {
            if (res.data.status == "SUCCESS") {
              this.$router.push("/user/examine-index");
            }
          });
        }
      });
    }
  },
  created() {}
};
</script>
